body{
  cursor: none;
}

.content-container {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}

.content-header {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 280px;
  display: flex;
  color: white;
  mix-blend-mode: difference;
  flex-direction: column;
}

.content-header-background {
  position: fixed;
  top: 0;
  left: 0;
  height: 400px;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 1) 0%,
      rgba(0, 0, 0, 0.7) 60%,
      rgba(0, 0, 0, 0) 100%);
}

.header-logo {
  font-family: 'Rubik Mono One';
  width: 100%;
  padding: 50px 0;
  height: 100px;
  font-size: 20px;
}

.header-menu {
  width: 100%;
  display: flex;
  font-family: 'Rubik';
  align-items: flex-start;
  font-size: 20px;
  justify-content: center;
}

.header-menu-item {
  z-index: 2;
  cursor: pointer;
  padding-top: 30px;
  margin: 0 6%;
  width: 140px;
}

.header-logo-video {
  height: 400px;
  position: relative;
  top: -150px;
}

.content-footer {
  width: 100%;
  padding-bottom: 80px;
}

.header-menu-item {
  display: block;
  color: white;
  text-decoration: none;
  position: relative;
  height: 30px;
}

.header-menu-item>span:after {
  content: '';
  position: absolute;
  left: 0;
  display: inline-block;
  height: 1em;
  width: 100%;
  border-bottom: 1px solid;
  margin-top: 10px;
  opacity: 0;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  -webkit-transform: scale(0, 1);
  transform: scale(0, 1);
}

.header-menu-item>span:hover:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

.content-background {
  position: relative;
  height: fit-content;
  width: 100%;
}

.content-background-img {
  width: 100%;
  -webkit-mask-image:-webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,1)), to(rgba(0,0,0,0)));
  mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
}

.iframe-container {
  position: absolute;
  top: 44.3%;
  left: 38%;
  height: 22.2%;
  width: 23.7%;
}

.cursor {
  pointer-events: none;
}

.cursor__ball {
  position: fixed;
  top: 0;
  left: 0;
  mix-blend-mode: difference;
  z-index: 1000;
}

.cursor__ball > svg > circle {
  fill: #f7f8fa;
}

.moving-board{
  z-index: 0;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.click-animation-container{
  position: absolute;
  left:240px;
  top:340px;
  height: 64px;
  width: 64px;
  transform: rotate(45deg);
}

.click-animation{
  height: 100%;
  width: 100%;
  background-image:url("Assets/click-icon.png");
  background-blend-mode:screen;
  animation: flowing 2s ease-in-out infinite alternate;
}

.click-box-1{
  position: absolute;
  left: 270px;
  top: 320px;
  width: 50px;
  height: 60px;
  z-index: 100;
}

.custom-modal{
  width: 500px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 40px;
  border: 1px solid #4ef542;
  background-color: black;
  color: white;
  z-index: 500;
  animation: fadeIn 1s;
}

.modal-close-icon{
  position: absolute;
  top: 20px;
  right: 28px;
  color: #4ef542;
}

@keyframes flowing {
	0% {
		transform: translateY(0);
	}
  50%{
    transform: translateY(20px);
  }
	100% {
		transform: translateY(0);
	}
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes fadeOut {
  0% { opacity: 1; }
  100% { opacity: 0; }
}